// CustomNode.js
import React from 'react';
import { Handle, Position } from '@xyflow/react';
import { stripHtmlTagsPreserveNewlines } from './stripHtmlTagsPreserveNewlines'; //    

const CustomNode = ({ id, data, selected }) => {
  const [idLine, ...textLines] = data.label.split('\n');
  const rawMainText = textLines.join('\n');
  const cleanMainText = stripHtmlTagsPreserveNewlines(rawMainText);
  const media = data.media || [];

  const firstPhoto = media.find((m) => m.type === 'photo');

  const handleSize = selected ? 20 : 16; //   Handle
  const sourceHandleStyle = {
    width: handleSize,
    height: handleSize,
    background: '#3b82f6',
    border: '3px solid white',
    borderRadius: '50%',
    boxShadow: '0 0 5px 2px rgba(59, 130, 246, 0.7)',
    transition: 'all 0.2s',
  };
  //      Handle
  const hotspotSize = 40; // , 40px  40px
  return (
    <div
      style={{
        backgroundColor: data.bgColor || '#ffffff',
        color: 'black',
        padding: 10,
        borderRadius: 10,
        minWidth: 160,
        maxWidth: 700, //   
        textAlign: 'center',
        boxShadow: '0 4px 12px rgba(0, 0, 0, 0.3)',
        border: selected ? '2px solid #3b82f6' : 'none',
        transition: 'border 0.2s',
        whiteSpace: 'normal', //   
        wordBreak: 'break-word', //    
        overflowWrap: 'break-word', //  
      }}
    >
      {/*  */}
      <div
        style={{
          backgroundColor: 'rgba(31, 41, 55, 0.7)',
          color: 'white',
          padding: '4px 8px',
          borderRadius: 6,
          marginBottom: 8,
          fontWeight: 'bold',
        }}
      >
        {idLine}
      </div>

      {/*   */}


      <div style={{
        whiteSpace: 'pre-wrap',     //   \n   
        textAlign: 'left',          //   
        wordBreak: 'break-word',    //    
        overflowWrap: 'break-word', //  
        fontSize: 14,
        marginBottom: firstPhoto ? 8 : 0
      }}>
        {cleanMainText}
      </div>


      {/*   */}
      {firstPhoto && (
        <img
          src={firstPhoto.url}
          alt="preview"
          style={{
            width: '100%',
            maxHeight: 100,
            objectFit: 'cover',
            borderRadius: 6,
          }}
        />
      )}

      <Handle type="target" position={Position.Top} style={{ visibility: 'hidden' }} />

      {/*      (hotspot)    */}
      <div
        style={{
          position: 'absolute',
          //  hotspot     
          bottom: -(hotspotSize / 2), //       ,  Handle   
          left: '50%',
          transform: 'translateX(-50%)',
          width: hotspotSize,  //  hotspot
          height: hotspotSize, //  hotspot
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          cursor: 'pointer',
          // background: 'rgba(255, 0, 0, 0.2)', //     
        }}
      >
        <Handle
          type="source"
          position={Position.Bottom} // Handle    hotspot 
          style={sourceHandleStyle}
          // isConnectable={true} // isConnectable   true,    
        />
      </div>
    </div>
  );
};

export default CustomNode;