// AttributeModal.js (    "")

import React, { useState, useEffect } from 'react';
import { apiGetStoryEffects } from './api';

export default function AttributeModal({ onSubmit, onCancel, userId, storyId }) {
  const [name, setName] = useState('');
  const [operator, setOperator] = useState('='); //   
  const [random, setRandom] = useState(false);
  const [value1, setValue1] = useState('');
  const [value2, setValue2] = useState('');
  const [effects, setEffects] = useState([]);
  const [loading, setLoading] = useState(false);
  const [showSuggestions, setShowSuggestions] = useState(false);

  //   : ,    ""
  const isDisplayMode = operator === 'display';

  useEffect(() => {
    //     
    async function loadEffects() {
      setLoading(true);
      const data = await apiGetStoryEffects(userId, storyId);
      setEffects(data || []);
      setLoading(false);
    }
    loadEffects();
  }, [userId, storyId]);

  const handleOk = () => {
    if (!name.trim()) return alert('  ');

    //    handleOk
    if (isDisplayMode) {
      //    ,     
      onSubmit({ name: name.trim(), isDisplay: true });
    } else {
      // ,    ( )
      let resultValue = '';
      if (random) {
        if (value1 === '' || value2 === '') return alert('  ');
        resultValue = `${operator}${value1}-${value2}`;
      } else {
        if (value1 === '') return alert(' ');
        resultValue = `${operator}${value1}`;
      }
      //    ,    
      onSubmit({ name: name.trim(), resultValue, isDisplay: false });
    }
  };

  const handleInputChange = (e) => {
    const value = e.target.value;
    setName(value);
    setShowSuggestions(value.trim() === '');
  };

  const handleSelectSuggestion = (effect) => {
    setName(effect);
    setShowSuggestions(false);
  };

  return (
    <div className="modal-overlay" onClick={onCancel}>
      <div className="modal-content small" onClick={(e) => e.stopPropagation()}>
        {/*        */}
        <h3>{isDisplayMode ? ' ' : ' '}</h3>
        <p style={{ color: '#777', fontSize: '13px', marginTop: '-4px', marginBottom: '12px' }}>
          {/*        */}
          {isDisplayMode
            ? '       .'
            : ',      ,         '}
        </p>

        <div className="form-row">
          <label>:</label>
          <div style={{ position: 'relative', width: '100%' }}>
            <input
              type="text"
              value={name}
              onChange={handleInputChange}
              placeholder=": "
              onFocus={() => setShowSuggestions(name.trim() === '')}
              onBlur={() => setTimeout(() => setShowSuggestions(false), 150)} //   
            />
            {showSuggestions && effects.length > 0 && (
              <ul
                className="suggestions-list"
                style={{
                  position: 'absolute',
                  top: '100%',
                  left: 0,
                  width: '100%',
                  background: '#fff',
                  border: '1px solid #ccc',
                  borderRadius: '6px',
                  maxHeight: '90px',
                  overflowY: 'auto',
                  zIndex: 1000,
                  fontSize: '13px',
                  boxShadow: '0 2px 6px rgba(0,0,0,0.1)',
                }}
              >
                {loading ? (
                  <li style={{ padding: '6px 10px', color: '#555' }}>...</li>
                ) : (
                  effects.map((effect) => (
                    <li
                      key={effect}
                      style={{
                        padding: '6px 10px',
                        cursor: 'pointer',
                        borderBottom: '1px solid #eee',
                        color: '#222',
                        backgroundColor: '#fff',
                      }}
                      onMouseEnter={(e) => (e.currentTarget.style.backgroundColor = '#f0f0f0')}
                      onMouseLeave={(e) => (e.currentTarget.style.backgroundColor = '#fff')}
                      onMouseDown={() => handleSelectSuggestion(effect)}
                    >
                      {effect}
                    </li>
                  ))
                )}
              </ul>
            )}
          </div>
        </div>

        <div className="form-row">
          <label>:</label> {/*   ""  ""   */}
          <select value={operator} onChange={(e) => setOperator(e.target.value)}>
            <option value="="> (=)</option>
            <option value="<"> (&lt;)</option>
            <option value=">"> (&gt;)</option>
            <option value="display"> </option> {/*    */}
          </select>
        </div>

        {/*        !isDisplayMode */}
        {!isDisplayMode && (
          <>
            <div className="form-row checkbox">
              <label>
                <input
                  type="checkbox"
                  checked={random}
                  onChange={(e) => setRandom(e.target.checked)}
                />
                
              </label>
            </div>

            {!random ? (
              <div className="form-row">
                <label>:</label>
                <input
                  type="number"
                  value={value1}
                  onChange={(e) => setValue1(e.target.value)}
                />
              </div>
            ) : (
              <div className="form-row">
                <label>:</label>
                <input
                  type="number"
                  value={value1}
                  onChange={(e) => setValue1(e.target.value)}
                />
                <label>:</label>
                <input
                  type="number"
                  value={value2}
                  onChange={(e) => setValue2(e.target.value)}
                />
              </div>
            )}
          </>
        )}

        <div className="modal-actions">
          <button className="modal-button secondary" onClick={onCancel}></button>
          <button className="modal-button primary" onClick={handleOk}>OK</button>
        </div>
      </div>
    </div>
  );
}