// HelpModal.js
import React from 'react';

const HelpModal = ({ onClose }) => {
  return (
    <div style={{
      position: 'fixed',
      top: '50%',
      left: '50%',
      transform: 'translate(-50%, -50%)',
      width: '80%',
      maxWidth: '600px',
      height: '70vh',
      backgroundColor: 'white',
      border: '1px solid #ccc',
      borderRadius: '8px',
      boxShadow: '0 4px 8px rgba(0, 0, 0, 0.1)',
      zIndex: 1001,
      display: 'flex',
      flexDirection: 'column',
    }}>
      <div style={{
        padding: '16px',
        borderBottom: '1px solid #eee',
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
      }}>
        <h2 style={{ margin: 0 }}>  </h2>
        <button onClick={onClose} style={{
          background: 'none',
          border: 'none',
          fontSize: '24px',
          cursor: 'pointer',
        }}>
          &times;
        </button>
      </div>
      <div style={{
        padding: '16px',
        overflowY: 'auto',
        flexGrow: 1,
      }}>
        <h3>  </h3>
        <p>
          - <strong>  :</strong>             .
          <br />
          - <strong>:</strong>            .
        </p>
        <blockquote style={{
          margin: '16px 0',
          padding: '10px 20px',
          background: '#f9f9f9',
          borderLeft: '5px solid #ccc'
        }}>
          <strong>:</strong>      ,   -,   .      .
        </blockquote>

        <hr style={{margin: '20px 0', border: 'none', borderTop: '1px solid #eee'}} />

        <h3>   </h3>
        <p>
          <strong>  :</strong>
          <ul>
            <li>
                          .
            </li>
            <li>
                   (      )      " ".
            </li>
          </ul>
        </p>
        <p>
          <strong> :</strong>
          <br />
             ,            .
        </p>

        <hr style={{margin: '20px 0', border: 'none', borderTop: '1px solid #eee'}} />

        <h3>  </h3>
        <p>       :</p>
        <ol>
          <li>
            <strong> :</strong>     (    /   )      .
          </li>
          <li>
            <strong> :</strong>   ,      ,    .
          </li>
          <li>
            <strong> :</strong>     (  )   ,      .
          </li>
        </ol>

        <hr style={{margin: '20px 0', border: 'none', borderTop: '1px solid #eee'}} />

        <h3>   ( )</h3>
        <p>
             ,   <code>Shift</code>  <code>Ctrl</code>     .
          <br />
           ,         ,      , ,         .
        </p>
      </div>
    </div>
  );
};

export default HelpModal;