// PaneContextMenu.js
import React from 'react';

/**
 *      () React Flow.
 * @param {object} props
 * @param {number} props.top -  Y  .
 * @param {number} props.left -  X  .
 * @param {Function} props.onClose -    .
 * @param {Function} props.onCreateFragment -     .
 * @param {Function} props.onAddNote -    .
 */
function PaneContextMenu({ top, left, onClose, onCreateFragment, onAddNote }) {
  const style = {
    position: 'absolute',
    top: `${top}px`,
    left: `${left}px`,
    zIndex: 1000,
  };

  const handleCreateFragment = () => {
    onCreateFragment();
    onClose();
  };

  const handleAddNote = () => {
    onAddNote();
    onClose();
  };

  return (
    <div className="context-menu" style={style}>
      <p>  </p>
      <button onClick={handleCreateFragment}>  </button>
      <button onClick={handleAddNote}>  </button>
      <button className="delete" onClick={onClose}> </button>
    </div>
  );
}

export default PaneContextMenu;