// stripHtmlTagsPreserveNewlines.js
/**
 *  HTML-  ,   \n  .
 * @param {string} input
 * @returns {string}
 */
export function stripHtmlTagsPreserveNewlines(input) {
  if (!input) return '';

  //     <tag ...>...</tag>  <tag />
  //   <a>, <span>, <blockquote expandable>, etc
  return input
    .replace(/<[^>]+>/g, '')   //   
    .replace(/<\/?[^>]+(>|$)/g, '') //  
    .replace(/&nbsp;/g, ' ')   //  
    .replace(/&lt;/g, '<')     // html entity
    .replace(/&gt;/g, '>')     //
    .replace(/&amp;/g, '&');   //
}