
// effectUtils.js

/**
 *      ,   UI.
 * @param {string} valueStr -   "10", "+5", ">2", "5-10", "-(5-10)"
 * @returns {object} -    type, value, from, to  ..
 */
export function parseEffectValue(valueStr) {
    if (typeof valueStr !== 'string') valueStr = String(valueStr || '');

    //   "  ", e.g., "+(5-10)" or "-(-2-8)"
    const modifyRangeMatch = valueStr.match(/^([+-])\((\-?\d+)-(\-?\d+)\)$/);
    if (modifyRangeMatch) {
        return {
            type: 'modifyRange',
            modifyType: modifyRangeMatch[1] === '+' ? 'increase' : 'decrease',
            from: modifyRangeMatch[2],
            to: modifyRangeMatch[3],
        };
    }

    //   "", e.g., "5-10" or "6--9"
    const rangeMatch = valueStr.match(/^(\-?\d+)-(\-?\d+)$/);
    if (rangeMatch) {
        return {
            type: 'range',
            from: rangeMatch[1],
            to: rangeMatch[2],
        };
    }

    //   ""  "", e.g., "+5", "-10", ">8", "<4", "=1"
    const prefixMatch = valueStr.match(/^([+-><=])(\-?\d+)$/);
    if (prefixMatch) {
        const char = prefixMatch[1];
        const value = prefixMatch[2];
        if (char === '+' || char === '-') {
            return { type: 'modify', modifyType: char === '+' ? 'increase' : 'decrease', value };
        }
        if (char === '>' || char === '<' || char === '=') {
            const checkTypeMap = { '>': 'greater', '<': 'less', '=': 'equal' };
            return { type: 'check', checkType: checkTypeMap[char], value };
        }
    }
    
    //   ""
    return { type: 'set', value: valueStr };
}


/**
 *     UI      modifiers.
 * @param {object} effect -     React.
 * @returns {{value: string, modifiers: Array}} -     .
 */
export function serializeEffectValue(effect) {
    let value = '';
    let modifiers = effect.modifiers || [];

    switch (effect.type) {
        case 'set':
            value = String(effect.value || '0');
            break;
        case 'modify':
            const sign = effect.modifyType === 'increase' ? '+' : '-';
            value = `${sign}${effect.value || '0'}`;
            break;
        case 'check':
            const op = { greater: '>', less: '<', equal: '=' }[effect.checkType];
            value = `${op}${effect.value || '0'}`;
            break;
        case 'range':
            value = `${effect.from || '0'}-${effect.to || '0'}`;
            break;
        case 'modifyRange':
            const modSign = effect.modifyType === 'increase' ? '+' : '-';
            value = `${modSign}(${effect.from || '0'}-${effect.to || '0'})`;
            break;
        default:
            value = '';
    }

    //  ,    
    if (effect.type !== 'range' && effect.type !== 'modifyRange') {
        modifiers = [];
    }

    return { value, modifiers };
}