// CustomEdge.js
import React from 'react';
import { getBezierPath, EdgeLabelRenderer } from '@xyflow/react';

const CustomEdge = ({
  id,
  sourceX,
  sourceY,
  targetX,
  targetY,
  sourcePosition,
  targetPosition,
  style = {},
  data, //    : text  effectsText
}) => {
  //     @xyflow/react  SVG-   
  //       
  const [edgePath, labelX, labelY] = getBezierPath({
    sourceX,
    sourceY,
    sourcePosition,
    targetX,
    targetY,
    targetPosition,
  });

  //  ID  -,    
  const markerEndId = `arrow-${id}`;
  
  // Interaction (onEdgeClick)  , ..    .
  //      .

  return (
    <>
      {/*   SVG- (  ) ,
             HTML- (div)   EdgeLabelRenderer.
              .
      */}

      {/*    -.  . */}
      <svg style={{ height: 0, width: 0, position: 'absolute' }}>
        <defs>
          <marker
            id={markerEndId}
            viewBox="0 0 10 10"
            refX="10"
            refY="5"
            markerWidth="6"
            markerHeight="6"
            orient="auto-start-reverse"
          >
            {/*     -  */}
            <path d="M 0 0 L 10 5 L 0 10 z" fill="#2563eb" />
          </marker>
        </defs>
      </svg>

      {/*    */}
      <path
        id={id}
        style={style} //       
        className="react-flow__edge-path" //    
        d={edgePath} // SVG-,  
        markerEnd={`url(#${markerEndId})`} // ,       
      />

      {/*     HTML- ( )    */}
      <EdgeLabelRenderer>
        <div
          style={{
            position: 'absolute',
            // transform   div'   (labelX, labelY)
            transform: `translate(-50%, -50%) translate(${labelX}px,${labelY}px)`,
            //  ,     ,   
            background: data?.effectsText ? '#dbeafe' : '#f1f5f9',
            padding: '4px 8px',
            borderRadius: '5px',
            fontSize: '12px',
            textAlign: 'center',
            color: '#000',
            //         ,
            //      .
            pointerEvents: 'all' 
          }}
          //    React Flow          
          className="nodrag nopan" 
        >
          {/*     */}
          <strong>{data?.text}</strong>
          
          {/*    ,    .
             data.effectsText,       .
          */}
          {data?.effectsText && (
            <div style={{ fontSize: '10px', color: '#334155', marginTop: '3px' }}>
               {data.effectsText}
            </div>
          )}
        </div>
      </EdgeLabelRenderer>
    </>
  );
};

export default CustomEdge;