// UploadMediaButton.js

import React, { useState } from 'react';

export default function UploadMediaButton({ userId, onUpload, onUploadStart, onUploadEnd }) {
  const handleFileChange = async (e) => {
    const file = e.target.files[0];
    if (!file) return;

    const formData = new FormData();
    formData.append('file', file);
    formData.append('user_id', userId); // <--   userId

    if (onUploadStart) onUploadStart();

    try {
      const res = await fetch('/api/upload_media', {
        method: 'POST',
        body: formData,
      });
      const result = await res.json();
      if (res.ok && result.file_id) {
        onUpload(result);
      } else {
        alert(result.error || '    Telegram');
      }
    } catch (err) {
      console.error('   :', err);
      alert('    . . .');
    } finally {
      if (onUploadEnd) onUploadEnd();
      e.target.value = null;
    }
  };

  return (
    <label className="modal-button primary" style={{ cursor: 'pointer', display: 'inline-block' }}>
        
      <input type="file" style={{ display: 'none' }} onChange={handleFileChange} accept="image/*,video/*,audio/*,.gif" />
    </label>
  );
}