// EffectRow.js

import React, { useState } from 'react';
import ProbabilityModal from './ProbabilityModal'; //    

//   
const rowStyle = {
    display: 'flex', //   grid  flex
    flexDirection: 'column', //     ( )
    gap: '10px', //   
    border: '1px solid #ccc',
    padding: '10px',
    borderRadius: '8px',
    background: '#f9f9f9',
};
const inputStyle = { padding: '8px', borderRadius: '4px', border: '1px solid #ddd', width: '100%', boxSizing: 'border-box' };
const selectStyle = { ...inputStyle };
const buttonStyle = { padding: '8px 12px', borderRadius: '4px', border: 'none', cursor: 'pointer' };
const smallButtonStyle = {
    ...buttonStyle,
    padding: '4px 8px',
    fontSize: '12px',
    maxWidth: '100px', //  
    whiteSpace: 'normal', //   
    textAlign: 'center', //   
    lineHeight: '1.2', //   
};

function EffectRow({ effect, onChange, onDelete, availableStats = [] }) {
    const [isProbModalOpen, setIsProbModalOpen] = useState(false);

    const handleChange = (field, value) => {
        onChange({ ...effect, [field]: value });
    };

    const renderValueInput = () => {
        switch (effect.type) {
            case 'set':
                return <input type="number" style={inputStyle} placeholder="" value={effect.value || ''} onChange={(e) => handleChange('value', e.target.value)} />;
            case 'modify':
                return (
                    <div style={{ display: 'flex', gap: '5px' }}>
                        <select style={selectStyle} value={effect.modifyType || 'increase'} onChange={(e) => handleChange('modifyType', e.target.value)}>
                            <option value="increase"></option>
                            <option value="decrease"></option>
                        </select>
                        <input type="number" style={inputStyle} placeholder="..." value={effect.value || ''} onChange={(e) => handleChange('value', e.target.value)} />
                    </div>
                );
            case 'check':
                return (
                    <div style={{ display: 'flex', gap: '5px' }}>
                        <select style={selectStyle} value={effect.checkType || 'equal'} onChange={(e) => handleChange('checkType', e.target.value)}>
                            <option value="greater">{'>'} ()</option>
                            <option value="less">{'<'} ()</option>
                            <option value="equal">{'='} ()</option>
                        </select>
                        <input type="number" style={inputStyle} placeholder="" value={effect.value || ''} onChange={(e) => handleChange('value', e.target.value)} />
                    </div>
                );
            case 'range':
            case 'modifyRange':
                return (
                    <div style={{ display: 'flex', alignItems: 'center', gap: '8px' }}>
                        <input type="number" style={inputStyle} placeholder="" value={effect.from || ''} onChange={(e) => handleChange('from', e.target.value)} />
                        <span>-</span>
                        <input type="number" style={inputStyle} placeholder="" value={effect.to || ''} onChange={(e) => handleChange('to', e.target.value)} />
                        <button style={{ ...smallButtonStyle, background: '#e0e0e0' }} onClick={() => setIsProbModalOpen(true)}>
                              
                        </button>
                    </div>
                );
            default:
                return null;
        }
    };

    return (
        <div style={rowStyle}>
            {/* ---  :     --- */}
            <div style={{ display: 'flex', gap: '10px' }}>
                <div style={{ flex: 2 }}>
                <input
                  type="text"
                  placeholder=" (, ...)"
                  value={effect.stat}
                  onChange={(e) => handleChange('stat', e.target.value)}
                  list={`available-stats-${effect.id}`}
                  style={inputStyle}
                />
                <datalist id={`available-stats-${effect.id}`}>
                  {availableStats.map((stat) => (
                    <option key={stat} value={stat} />
                  ))}
                </datalist>
                </div>

                <div style={{ flex: 1 }}>
                    <select style={selectStyle} value={effect.type} onChange={(e) => handleChange('type', e.target.value)}>
                        <option value="set"></option>
                        <option value="modify"></option>
                        <option value="check"></option>
                        <option value="range"></option>
                        <option value="modifyRange">  </option>
                    </select>
                </div>
                {/*  /    */}
                {effect.type === 'modifyRange' && (
                    <div style={{ flex: 1 }}>
                        <select
                            style={selectStyle}
                            value={effect.modifyType || 'increase'}
                            onChange={(e) => handleChange('modifyType', e.target.value)}
                        >
                            <option value="increase"></option>
                            <option value="decrease"></option>
                        </select>
                    </div>
                )}
            </div>

            {/* ---  :    --- */}
            <div style={{ display: 'flex', gap: '10px', alignItems: 'center' }}>
                {/*      (   ) */}
                <div style={{ flex: 1 }}>
                    {renderValueInput()}
                </div>
                
                {/*    */}
                <div style={{ display: 'flex', alignItems: 'center', gap: '10px' }}>
                    <label title="   " style={{ display: 'flex', alignItems: 'center', gap: '5px', cursor: 'pointer' }}>
                        <input type="checkbox" checked={!!effect.hide} onChange={(e) => handleChange('hide', e.target.checked)} />
                        
                    </label>
                    <button style={{ ...buttonStyle, background: '#f8d7da', color: '#721c24' }} onClick={onDelete}>
                        
                    </button>
                </div>
            </div>
            
            {/*    (  ) */}
            {isProbModalOpen && (
                <ProbabilityModal
                    isOpen={isProbModalOpen}
                    onClose={() => setIsProbModalOpen(false)}
                    from={effect.from}
                    to={effect.to}
                    modifiers={effect.modifiers || []}
                    onSave={(newModifiers) => {
                        handleChange('modifiers', newModifiers);
                        setIsProbModalOpen(false);
                    }}
                />
            )}
        </div>
    );
}

export default EffectRow;