import React, { useState, useEffect } from 'react';
import { apiGetStoryList, apiCreateStory, apiDeleteStory, apiUpdateStoryPublic } from './api';

const StoryListPage = ({ userId }) => {
  const [stories, setStories] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [selectedStories, setSelectedStories] = useState([]);
  const [shareStory, setShareStory] = useState(null); // <--    ""

  useEffect(() => {
    const fetchStories = async () => {
      try {
        setLoading(true);
        const storyList = await apiGetStoryList(userId);
        setStories(storyList);
        setError(null);
      } catch (err) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };
    fetchStories();
  }, [userId]);

  const handleCreateNew = async () => {
    const title = prompt('    :');
    if (!title || !title.trim()) return alert('    .');

    const tgUser = window.Telegram?.WebApp?.initDataUnsafe?.user;
    const userName = tgUser?.username || 'unknown_user';
    const authorName = tgUser?.first_name || userName;

    try {
      const newStory = await apiCreateStory(userId, title.trim(), userName, authorName);
      window.location.href = `/${userId}_${newStory.story_id}`;
    } catch (err) {
      alert(`   : ${err.message}`);
    }
  };

  const handleDelete = async (storyId, title) => {
    if (!window.confirm(`  ${title}?   .`)) return;
    try {
      await apiDeleteStory(userId, storyId);
      setStories(prev => prev.filter(story => story.id !== storyId));
    } catch (err) {
      alert(`  : ${err.message}`);
    }
  };

  const handleBulkDelete = async () => {
    if (!window.confirm(` ${selectedStories.length}  ?`)) return;
    try {
      await Promise.all(selectedStories.map(id => apiDeleteStory(userId, id)));
      setStories(prev => prev.filter(story => !selectedStories.includes(story.id)));
      setSelectedStories([]);
    } catch (err) {
      alert(`  : ${err.message}`);
    }
  };

  const handleCheckboxChange = (storyId) => {
    setSelectedStories(prev =>
      prev.includes(storyId)
        ? prev.filter(id => id !== storyId)
        : [...prev, storyId]
    );
  };

  const handleShare = (story) => setShareStory(story);
  const closeShareModal = () => setShareStory(null);

  const copyToClipboard = (text) => {
    navigator.clipboard.writeText(text);
    alert('!');
  };

  return (
    <div className="container-dark">
    <style>{`
      .container-dark {
        padding: 20px;
        max-width: 800px;
        margin: 0 auto;
        font-family: Arial, sans-serif;
        background-color: #1e1e1e;
        color: #e0e0e0;
        min-height: 100vh;
      }
      h1 {
        font-size: 24px;
        margin-bottom: 20px;
        text-align: center;
        color: #fff;
      }
      ul {
        list-style: none;
        padding: 0;
        margin: 0;
      }
      li {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 12px 15px;
        border: 1px solid #333;
        margin-bottom: 10px;
        border-radius: 10px;
        background-color: #2a2a2a;
        transition: background-color 0.2s;
        flex-wrap: nowrap; /*        */
      }
      li:hover {
        background-color: #333;
      }
      .story-header {
        display: flex;
        align-items: center;
        flex: 1;
        min-width: 0;
      }
      input[type="checkbox"] {
        transform: scale(1.3);
        margin-right: 10px;
        flex-shrink: 0;
      }
      .story-title {
        font-size: 18px;
        flex: 1;
        word-break: break-word;
      }
      .buttons {
        display: flex;
        gap: 8px;
        flex-shrink: 0;
        flex-wrap: nowrap;
      }
      button, a {
        text-decoration: none;
        padding: 8px 12px;
        color: white;
        border-radius: 6px;
        cursor: pointer;
        border: none;
        font-size: 14px;
        transition: opacity 0.2s;
      }
      .edit-btn { background-color: #007bff; }
      .del-btn { background-color: #dc3545; }
      .share-btn { background-color: #6c757d; }
      .create-btn, .bulk-del-btn {
        display: block;
        width: 100%;
        padding: 12px;
        font-size: 16px;
        border: none;
        border-radius: 8px;
        cursor: pointer;
        margin-top: 20px;
        text-align: center;
      }
      .create-btn { background-color: #28a745; color: white; }
      .bulk-del-btn { background-color: #d9534f; color: white; }

      /* ===   === */
      .modal-overlay {
        position: fixed;
        top: 0; left: 0; right: 0; bottom: 0;
        background-color: rgba(0,0,0,0.7);
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 1000;
      }
      .modal-content {
        background-color: #2b2b2b;
        border-radius: 12px;
        padding: 25px;
        max-width: 500px;
        width: 90%;
        color: #f1f1f1;
        box-shadow: 0 0 20px rgba(0,0,0,0.5);
        animation: fadeIn 0.3s ease;
      }
      @keyframes fadeIn {
        from {opacity: 0; transform: scale(0.9);}
        to {opacity: 1; transform: scale(1);}
      }
      .modal-content h2 {
        text-align: center;
        margin-bottom: 20px;
        color: #fff;
      }
      .modal-content p {
        margin-bottom: 15px;
        line-height: 1.4;
      }
      .link-box {
        display: flex;
        align-items: center;
        background-color: #1e1e1e;
        border-radius: 8px;
        padding: 8px 10px;
        justify-content: space-between;
        word-break: break-all;
      }
      .copy-btn {
        background-color: #444;
        border: none;
        border-radius: 6px;
        color: #fff;
        padding: 5px 10px;
        cursor: pointer;
      }
      .copy-btn:hover {
        background-color: #666;
      }
      .modal-footer {
        text-align: center;
        margin-top: 25px;
      }
      .close-btn {
        background-color: #555;
        color: white;
        border: none;
        border-radius: 8px;
        padding: 8px 14px;
        cursor: pointer;
      }
      .close-btn:hover {
        background-color: #777;
      }
      label {
        display: flex;
        align-items: center;
        gap: 8px;
        margin-top: 15px;
        font-size: 15px;
      }

      /* ===   === */
      @media (max-width: 600px) {
        li {
          flex-direction: column;
          align-items: flex-start;
          flex-wrap: wrap;
        }
        .story-header {
          width: 100%;
          margin-bottom: 6px;
        }
        .buttons {
          width: 100%;
          justify-content: flex-start;
          flex-wrap: wrap;
          margin-top: 10px; /*      */
        }
      }
    `}</style>


      <h1> </h1>
      {loading && <p> ...</p>}
      {error && <p style={{ color: 'red' }}>: {error}</p>}

      {!loading && !error && (
        <>
          <ul>
            {stories.length > 0 ? (
              stories.map(story => (
                <li key={story.id}>
                  <div className="story-header">
                    <input
                      type="checkbox"
                      checked={selectedStories.includes(story.id)}
                      onChange={() => handleCheckboxChange(story.id)}
                    />
                    <span className="story-title">{story.title}</span>
                  </div>
                  <div className="buttons">
                    <a href={`/${userId}_${story.id}`} className="edit-btn">
                      .
                    </a>
                    <button className="del-btn" onClick={() => handleDelete(story.id, story.title)}>
                      
                    </button>
                    <button className="share-btn" onClick={() => handleShare(story)}>
                      
                    </button>
                  </div>
                </li>
              ))
            ) : (
              <p style={{ textAlign: 'center' }}>    .</p>
            )}
          </ul>

          {selectedStories.length > 0 && (
            <button onClick={handleBulkDelete} className="bulk-del-btn">
                ({selectedStories.length})
            </button>
          )}

          <button onClick={handleCreateNew} className="create-btn">
            +   
          </button>
        </>
      )}

      {shareStory && (
        <div className="modal-overlay" onClick={closeShareModal}>
          <div className="modal-content" onClick={e => e.stopPropagation()}>
            <h2> </h2>
            <p>     <b>{shareStory.title}</b>   3 :</p>

            <p><b>1)</b>   .    ,      :</p>
            <div className="link-box">
              <span>{`https://t.me/FoxNovel_bot?start=${shareStory.id}`}</span>
              <button className="copy-btn" onClick={() => copyToClipboard(`https://t.me/FoxNovel_bot?start=${shareStory.id}`)}></button>
            </div>

            <p><b>2)</b>   Telegram-  <b>@FoxNovel_bot</b>    :</p>
            <div className="link-box">
              <span>@FoxNovel_bot</span>
              <button className="copy-btn" onClick={() => copyToClipboard('@FoxNovel_bot')}></button>
            </div>

            <p><b>3)</b>       .      :</p>
            <label>
              <input
                type="checkbox"
                checked={!!shareStory.public}
                onChange={async (e) => {
                  const newStatus = e.target.checked;
                  let userName = shareStory.user_name || '';

                  //       
                  if (newStatus) {
                    userName = prompt('  ,   :', userName || '');
                    if (!userName) {
                      alert('     .');
                      e.target.checked = false;
                      return;
                    }
                  }

                  try {
                    const result = await apiUpdateStoryPublic(userId, shareStory.id, newStatus, userName);
                    setShareStory({ ...shareStory, public: newStatus, user_name: result.user_name });
                    setStories(prev =>
                      prev.map(s =>
                        s.id === shareStory.id ? { ...s, public: newStatus, user_name: result.user_name } : s
                      )
                    );
                  } catch (err) {
                    alert('   : ' + err.message);
                  }
                }}
              />
                 
            </label>

            <div className="modal-footer">
              <button className="close-btn" onClick={closeShareModal}></button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default StoryListPage;
