export async function apiUpdateNote(userId, storyId, noteId, text) {
    const response = await fetch(`/api/story/${userId}/${storyId}/bookmarks/${noteId}`, {
        method: 'PUT', //   PUT  
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ text }), //     
    });
    if (!response.ok) {
        const { error } = await response.json();
        throw new Error(error || 'Server error on update');
    }
    return await response.json(); //   {"status": "ok"}
}

