// EdgeContextMenu.js
import React from 'react';

//     .      CSS-,  .
const menuStyle = {
  position: 'absolute',
  background: 'white',
  padding: '10px',
  borderRadius: '8px',
  boxShadow: '0 2px 10px rgba(0,0,0,0.2)',
  zIndex: 100,
  display: 'flex',
  flexDirection: 'column',
  gap: '8px',
  minWidth: '220px' //     
};

const buttonStyle = {
  background: '#f8f9fa',
  border: '1px solid #dee2e6',
  borderRadius: '4px',
  padding: '8px 12px',
  cursor: 'pointer',
  textAlign: 'left',
  width: '100%',
  display: 'flex', //     
  alignItems: 'center',
  gap: '8px', //     
};

// userId  storyId   API-  .
// onEditEffects -    App.js,    .
// setEdges -       .
function EdgeContextMenu({ top, left, edge, onClick, onEditEffects, setEdges, userId, storyId }) {
  //      
  const { source, target, data } = edge;

  // ---   ---

  // 1.      prompt()
  const handleEditText = async () => {
    const newText = prompt("     :", data.text);
    
    //    ""    , 
    if (newText === null || newText === data.text) {
      onClick(); //     
      return;
    }
    
    try {
      //    ,    
      await fetch(`/api/story/${userId}/${storyId}/choice`, {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          source: source,
          choiceIndex: data.choiceIndex,
          text: newText,
          // effects  ,    
        }),
      });
      
      //      
      setEdges((eds) =>
        eds.map((e) => {
          if (e.id === edge.id) {
            return { ...e, data: { ...e.data, text: newText } };
          }
          return e;
        })
      );

    } catch (error) {
      alert(`  : ${error.message}`);
    } finally {
        onClick(); //    
    }
  };

  // 2.  
  const handleDelete = async () => {
    if (window.confirm(` ,      "${source}"  "${target}"?`)) {
      try {
        //    
        const response = await fetch(`/api/story/${userId}/${storyId}/choice`, {
            method: 'DELETE',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
                source: source,
                choiceIndex: data.choiceIndex //     
            }),
        });

        if (!response.ok) {
            throw new Error('  ');
        }

        //     ,     
        setEdges((eds) => eds.filter((e) => e.id !== edge.id));
      } catch (e) {
          alert(`  : ${e.message}`);
      }
    }
    onClick(); //  
  };

  return (
    // style={{ top, left }}   ,   
    // onClick={(e) => e.stopPropagation()}        
    <div style={{ ...menuStyle, top, left }} onClick={(e) => e.stopPropagation()}>
        {/*     */}
        <div style={{ padding: '4px 8px', fontWeight: 'bold', borderBottom: '1px solid #eee', color: '#333' }}>
            : {source}  {target}
        </div>
      
        {/*    */}
        <button style={buttonStyle} onClick={handleEditText}>
              
        </button>
      
        {/*   .      App.js */}
        <button style={buttonStyle} onClick={onEditEffects}>
              
        </button>
      
        {/*   */}
        <button style={{...buttonStyle, color: '#dc3545'}} onClick={handleDelete}>
              
        </button>
      
        {/*    */}
        <button style={buttonStyle} onClick={onClick}>
             
        </button>
    </div>
  );
}

export default EdgeContextMenu;