// client/src/AppRouter.js ( )

import React from 'react';
import { ReactFlowProvider } from '@xyflow/react';

//    -""
import App from './App'; //   App.js ()
import StoryListPage from './StoryListPage'; //    ()

const AppRouter = () => {
  const pathname = window.location.pathname;

  //      /userid_storyid
  //  : / + (-__) + _ + (-)
  const editorRegex = /^\/([^_]+)_(.+)$/;
  
  //      /userid
  //  : / + (-__) +  
  const listRegex = /^\/([^_]+)$/;

  const editorMatch = pathname.match(editorRegex);
  const listMatch = pathname.match(listRegex);

  if (editorMatch) {
    // : /userid_storyid
    //    App.js ()
    //    ReactFlowProvider,    App.js
    return (
      <ReactFlowProvider>
        <App />
      </ReactFlowProvider>
    );
  } else if (listMatch) {
    // : /userid
    //     
    const userId = listMatch[1]; //  userid  URL
    return <StoryListPage userId={userId} />;
  } else {
    //   ,    /userid  /userid_storyid
    // ,  -   /
    return <div> . <br/> ,     Telegram .</div>;
  }
};

export default AppRouter;