// NoteContextMenu.js
import React, { useState, useEffect, useCallback } from 'react';
import { TextEditorModal, ActionModal } from './ContextMenu';
import { apiUpdateNote, apiDeleteNote } from './api'; // ,     

//  ID     
const pathname = window.location.pathname;
const [userId, storyId] = pathname.slice(1).split('_');

function NoteContextMenu({ node, setNodes, onClose, top, left, autoOpenEditor = false }) {
  const [modal, setModal] = useState(null);

  //     
  useEffect(() => {
    if (autoOpenEditor) {
      setModal('edit');
    }
  }, [autoOpenEditor]);
  useEffect(() => {
    console.log('[DEBUG] NoteContextMenu  :', node?.id);
  }, [node]);


    //     
    const handleSaveText = useCallback(async (newText) => {
        try {
            // 1.  API   
            await apiUpdateNote(userId, storyId, node.id, newText);

            // 2.      
            setNodes((nds) =>
                nds.map((n) => {
                    if (n.id === node.id) {
                        return { ...n, data: { ...n.data, text: newText } };
                    }
                    return n;
                })
            );
        } catch (e) {
            alert(`  : ${e.message}`);
        } finally {
            setModal(null); //  
            onClose(); //   
        }
    }, [node.id, setNodes, onClose]);

    //   
    const handleDelete = useCallback(async () => {
        try {
            // 1.  API  
            await apiDeleteNote(userId, storyId, node.id);
            // 2.    
            setNodes((nds) => nds.filter((n) => n.id !== node.id));
        } catch (e) {
            alert(`  : ${e.message}`);
        } finally {
            setModal(null);
            onClose();
        }
    }, [node.id, setNodes, onClose]);

    //   
      const style = {
        top: `${top}px`,
        left: `${left}px`,
        position: 'fixed', //    'fixed'  
        zIndex: 1000
      };

      return (
        <>
          {!autoOpenEditor && ( //    -
            <div style={style} className="context-menu">
              <p>: <strong>{node.id}</strong></p>
              <button onClick={() => setModal('edit')}> </button>
              <button className="delete" onClick={() => setModal('delete')}> </button>
              <button onClick={onClose}> </button>
            </div>
          )}

          {modal === 'edit' && (
            <TextEditorModal
              initialHtml={node.data.text || ''}
              onSave={handleSaveText}
              onCancel={() => { setModal(null); onClose(); }}
            />
          )}

            {modal === 'delete' && (
                <ActionModal
                    title=" "
                    type="danger"
                    onConfirm={handleDelete}
                    onCancel={() => { setModal(null); onClose(); }}
                    confirmText=", "
                >
                    <p> ,    ?</p>
                </ActionModal>
            )}
        </>
    );
}

export default NoteContextMenu;